import React, { useState, useEffect } from 'react';
import './Header.css';

function Header() {

  const [showHeaderBar, setShowHeaderBar] = useState(true);

  const contactHandler = () => {
    setShowHeaderBar(!showHeaderBar);
  }

  return (
    <div className="header">
      <div id="header__bar" style={{ transform: showHeaderBar ? `translateY(120px)` : `translateY(0px)` }}>
        <div className="header__bar__icons">
          <a href="https://builtin.com/data-science/bubble-sort-time-complexity" target='_blank'><button className="icon-button">Bubble Sort</button></a>
          <a href="https://www.geeksforgeeks.org/merge-sort/" target='_blank'><button className="icon-button">Merge Sort</button></a>
          <a href="https://www.geeksforgeeks.org/insertion-sort/" target='_blank'><button className="icon-button">Insertion Sort</button></a>
          <a href="https://www.studysmarter.co.uk/explanations/computer-science/algorithms-in-computer-science/selection-sort/#:~:text=Selection%20Sort%20has%20a%20time,the%20size%20of%20the%20input." target='_blank'><button className="icon-button">Selection Sort</button></a>
          <a href="https://www.simplilearn.com/tutorials/data-structure-tutorial/quick-sort-algorithm#:~:text=The%20average%2Dcase%20time%20complexity,pivot%20selection%20is%20commonly%20used." target='_blank'><button className="icon-button">Quick Sort</button></a>
        </div>
      </div>

      <div className="header-logo">Algorithm <span> Visualizer</span></div>
      <div className="header__options">
        <h3 className="header__option" onClick={contactHandler}><span>About</span> algorithm</h3>
      </div>
    </div>
  )
}

export default Header;
